* {
   box-sizing: border-box;
}

html {
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
   padding: 0;
   margin: 0;
}

img{
   width: 100%;
}

h1, h2, h3, h4, h5, h6, p{
   padding: 0;
   margin: 0;
}

a {
   text-decoration: none;
   color: white;
}